<template>
  <div class="weather-app" :style="{ backgroundImage: 'url(' + bgImage + ')' }">
    <div class="searchBar">
      <input
        type="text"
        placeholder="Search..."
        class="searchInput"
        v-model="query"
        @keypress="fetchData"
      />
    </div>
    <div
      class="weather-wrap"
      v-if="weather && weather.current && weather.location"
    >
      <div class="location-box">
        <div class="location">
          {{ weather.location.name }}, {{ weather.location.country }}
        </div>
        <div class="date">{{ getDateInfo() }}</div>
      </div>
      <div class="weather-box">
        <div class="temp">{{ weather.current.temp_c }} C</div>
        <div class="weather">{{ weather.current.condition.text }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "weather-app",
  data() {
    return {
      query: "",
      bgImage:
        "https://cdn.pixabay.com/photo/2014/07/28/20/39/sunset-404072__480.jpg",
      imgUrl: "https://pixabay.com/api/?key=22534946-29a118275c10d7f6f3d354c0b",
      apiKey: "53c6a7cb6ea943aaa0932419211207",
      url_base: "https://api.weatherapi.com/v1/current.json",
      weather: {}
    };
  },
  methods: {
    fetchData: function(e) {
      if (e.key === "Enter") {
        fetch(`${this.url_base}?key=${this.apiKey}&q=${this.query}`)
          .then(res => {
            if (res.ok) {
              return res.json();
            }
            throw Error("Invalid API key");
          })
          .then(this.setImage);
      }
    },
    setImage(res) {
      let img = this.bgImage;
      this.weather = res;
      var weatherName = res.current.condition.text;
      var weatherTxt =
        weatherName.split(" ").length > 0
          ? weatherName.split(" ").join("+")
          : weatherName;
      fetch(`${this.imgUrl}&q=${weatherTxt}+mountain&cat=nature`)
        .then(data => {
          return data.json();
        })
        .then(respo => {
          if (respo.hits.length === 0) {
            this.bgImage = img;
          } else {
            this.bgImage = respo.hits[1].largeImageURL;
          }
        });
    },
    getDateInfo() {
      let d = new Date();
      let months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ];
      let days = [
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday"
      ];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${date} ${month}, ${year} ${day}`;
    }
  }
};
</script>

